/* hamburger */

.login-container .top .hamburger-icon {
  position: absolute;
  right: 0;
  opacity: 0;
}

/* slide out menu */

.login-container .slide-menu {
  visibility: hidden;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1;
}

.login-container .slide-menu .menu-items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex: 1 1;
          flex: 1 1;
}

