  border-bottom: 1px solid #9e9c9a;
  margin: 0px;
  list-style: none;
}

.login-container .slide-menu li a {
  display: block;
  font-size: 1.2em;
  padding: 8px 0px;
  text-decoration: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 100;
  width: 100%;
  height: 100%;
  transition: background-color 500ms ease-in-out;
}

.login-container .slide-menu li a:hover {
  background-color: #464343;
  color: #fff;
  font-weight: bold;
}

.login-container .slide-menu .close-icon {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.login-container .invalid-input {
  line-height: 1;
  position: absolute;
  font-size: 0.88rem;
  left: 0;
  color: red;
  top: -10px;
}

@media only screen and (max-width: 800px) {
  .login-container .nav {
    display: none;
  }
  .login-container .top .hamburger-icon {
    opacity: 1;
  }

  .login-container .slide-menu {
    visibility: visible;
    background-color: #4d4d4d;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-transform: translatey(-165px);
            transform: translatey(-165px);
    transition: -webkit-transform 0.3s ease-in;
    transition: transform 0.3s ease-in;
    transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
  }

  .login-container #login-welcome{
    color: rgba(10, 49, 6, 0.849);
  }
}

